export function modeControl(timer) {
    console.log('Nous avons cliqué mode');
    var mode = timer.mode;
    if (mode >= 2) {
        timer.resetMode();
        alert("Vous avez désactiver les modifications");
    }
    else {
        timer.switchMode();
    }
}
export function increaseControl(timer) {
    var mode = timer.getMode(); //get mode value
    console.log('Nous avons cliqué increase', mode);
    if (mode == 1) { // Edit hour
        if (timer.getActualHour() < 23) { // control if hour is not > 23
            timer.incrementHour();
        }
        else {
            timer.setHouTtoZero(); // Reset hour to 00
        }
        resetHour(timer);
    }
    else {
        if (mode == 2) { // Edit Minutes
            if (timer.getActualMinute() < 59) { // incrémenter les minutes
                timer.incrementMinute();
            }
            else { // Reset les minutes à zero et incrementer l'heure
                timer.setMinuteToZero();
            }
            resetMinute(timer);
            resetHour(timer);
        }
        else {
            alert("Veuillez changer le mode");
        }
    }
}
export function lightControl(timer) {
    console.log('Nous avons cliqué mode');
}
function resetHour(timer) {
    document.getElementById('hour').innerText = timer.actualHour.toString();
}
function resetMinute(timer) {
    document.getElementById('minute').innerText = timer.actualMinute.toString();
}
