import './index.css';
import { Timer } from './example-unit';
import { modeControl, increaseControl, lightControl } from './example-unit';
// Créer une horloge
var timer = new Timer();
// initialiser l'horloge
export var currentDate = new Date();
export function initialize() {
    var currentTimeDate = new Date();
    console.log('means mode is 0', timer.mode);
    var hours = currentTimeDate.getHours();
    var minutes = currentTimeDate.getMinutes();
    // timer.setHour(hours)
    //  timer.setMinute(minutes)
    document.getElementById('hour').innerText = hours.toString();
    document.getElementById('minute').innerText = minutes.toString();
    setTimeout(initialize, 500);
}
initialize();
function initilizeButtons() {
    var modeBtn = document.getElementById("modeBtn");
    modeBtn.addEventListener("click", function (e) { return modeControl(timer); });
    var increaseBtn = document.getElementById("increaseBtn");
    increaseBtn.addEventListener("click", function (e) { return increaseControl(timer); });
    var lightBtn = document.getElementById("lightBtn");
    lightBtn.addEventListener("click", function (e) { return lightControl(timer); });
}
initilizeButtons();
